import { Order_Food } from "./Types";

export const Orderfood = (data) => (dispatch) => {
  console.log(data);
  dispatch({
    type: Order_Food,
    payload: data,
  });
};

export const postAddress = (data) => (dispatch) => {
    console.log(data);
    defaultAxios.post(`/user/PostAddress`, data).then((res) =>
      dispatch({
        type: Post_Add,
        payload: res.data,
      })
    );
  };
  